#content .wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 20px;
}

#content h1 {
  margin: 0;
  /* border: 1px dashed red;  */
  font-weight: 100;
  padding: 15px 15px 15px 0px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: -2px;
}

#content h2 {
  margin: 0;
  /* border: 1px dashed red;  */
  font-weight: bold;
  padding: 15px 15px 15px 0;
  /* background-color: #ff0050; */
  color: white;
  display: table;
  -webkit-transform: scale( 0.5 , 0.5 ) rotate(-30deg);
      -ms-transform: scale( 0.5 , 0.5 ) rotate(-30deg);
          transform: scale( 0.5 , 0.5 ) rotate(-30deg);
  background-color: rgba(255, 0, 80, 1);
  z-index: 1;
}

#content p {
  margin: 0;
  /* border: 1px dashed red;  */
  font-weight: 100;
  text-align: justify;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: #999;
  font-style: italic;
  padding: 15px 0 15px 0;
}

#content .image {

}

#content .image.small {
  width: 100%;
  height: 100px;
  overflow: hidden;
}

#content .image.large {
  width: 100%;
  height: 200px;
  overflow: hidden;
}

#content .image.nopadding {
  width: calc( 100% + 40px );
  margin-left: -20px;
}

/* image zoom in to 130% -- START */
#content .image.cropped-zoom-in .image-inner {
  opacity: 0;
  background-size: cover;
  background-position: center center;
  width: 100%;
  height: 100%;
}
/* image zoom in to 130% -- END */
#content .image.cropped-zoom-in .image-inner.el-show{
  opacity: 1!important;
  -webkit-transform: scale(1.3,1.3)!important;
      -ms-transform: scale(1.3,1.3)!important;
          transform: scale(1.3,1.3)!important;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-transition-timing-function: ease-out;
       -o-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}
/* image zoom out from 130% -- START */
#content .image.cropped-zoom-out .image-inner {
  opacity: 0;
  background-size: cover;
  background-position: center center;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(1.3,1.3);
      -ms-transform: scale(1.3,1.3);
          transform: scale(1.3,1.3);
}
/* image zoom out from 130% -- END */
#content .image.cropped-zoom-out .image-inner.el-show{
  opacity: 1!important;
  -webkit-transform: scale(1,1);
      -ms-transform: scale(1,1);
          transform: scale(1,1);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-transition-timing-function: ease-out;
       -o-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

#content .image-inner.el-show .image-preloader{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px dashed rgba(255, 0, 80, 1);
  -webkit-animation: image-preloader-spin 5s linear infinite;
          animation: image-preloader-spin 5s linear infinite;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  -webkit-transition-timing-function: ease-out;
       -o-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

@-webkit-keyframes image-preloader-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes image-preloader-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#content .image-inner.el-show .image-preloader-perc{
  position: static;
  width: 0px;
  overflow: visible;
}

#content .image-inner.el-show .image-preloader-perc .text{
  position: absolute;
  font-size: 14px;
  width: 50px;
  font-weight: bold;
  margin-left: -51px;
  margin-top: -8px;
  text-align: center;
  color: rgba(255, 0, 80, 1);
}

.el-show{
  opacity: 1!important;
  -webkit-transform: scale( 1 , 1 );
      -ms-transform: scale( 1 , 1 );
          transform: scale( 1 , 1 );
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-transition-timing-function: ease-out;
       -o-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

h2.el-show{
  opacity: 1!important;
  -webkit-transform: scale( 1 , 1 ) rotate(-1deg)!important;
      -ms-transform: scale( 1 , 1 ) rotate(-1deg)!important;
          transform: scale( 1 , 1 ) rotate(-1deg)!important;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
       -o-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
          transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55); 
}

p.el-show{
  -webkit-transition-delay: 0s;
       -o-transition-delay: 0s;
          transition-delay: 0s;
}

.image{
  background-size: 100% 100%;
}

/* mobile */

@media screen and (max-width: 768px) {

  #content h1 {
    font-size: 24px;
  }

  #content h2 {
    font-size: 18px;
    padding: 15px 15px 15px 15px;
  }

  #content p {
    font-size: 17px;
    line-height: 21px;
  }

  #content .image.small {
    height: 100px;
  }
  
  #content .image.large {
    height: 200px;
  }

}

/* desktop */

@media screen and (min-width: 768px) {

  #content h1 {
    font-size: 32px;
  }

  #content h2 {
    font-size: 18px;
    padding: 15px 15px 15px 15px;
  }

  #content p {
    font-size: 18px;
    line-height: 26px;
  }

  #content .image.small {
    height: 200px;
  }
  
  #content .image.large {
    height: 400px;
  }

}


