body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: visible;
}

::-webkit-scrollbar { 
  display: none; 
}

.centered {
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
}

.fullscreen {
  position: absolute;
  width: 100%;
  height: 100%;
}

a, a:hover, a:link, a:visited, a:active {
  color: #009DFF;
  font-weight: bold;
  text-decoration: underline;
  -webkit-text-decoration-color: #009DFF;
          text-decoration-color: #009DFF;
}

.lock-screen {
  height: 100%;
  overflow: hidden;
  width: 100%;
  position: fixed;
}

#app {
  position: absolute;
  width: 100%;
  height: 100%;
}
    
#loader {
  z-index: 10000;
  position: fixed ;
  top: 0px;
}

#block {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgba(255,255,255,1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 1000000;
}

#viewer {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgba(0,0,0,1);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 20000;
  display: -ms-flexbox;
  display: flex;
}

#debugger {
  position: fixed;
  top: 0px;
  bottom: 50%;
  left: 0px;
  right: 50%;
  background-color: rgba(0,0,0,0.5);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 200;
}

#overlay {
  z-index: 1000;
}

#navigation {
  z-index: 900;
  position: fixed ;
  top: 0px;
  width: 100%;
  overflow: hidden;
  /* border-bottom: solid 1px white; */
  -webkit-box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.1);
}

#content {
  z-index: 100;
  top: 0px;
  overflow: visible;
}

@-webkit-keyframes navigation-on {
  0% { -webkit-transform: translateY( calc( 40px - 100% ) ); transform: translateY( calc( 40px - 100% ) ); }
  100% { -webkit-transform: translateY( 0% ); transform: translateY( 0% ); }
}

@keyframes navigation-on {
  0% { -webkit-transform: translateY( calc( 40px - 100% ) ); transform: translateY( calc( 40px - 100% ) ); }
  100% { -webkit-transform: translateY( 0% ); transform: translateY( 0% ); }
}

@-webkit-keyframes navigation-off {
  0% { -webkit-transform: translateY( 0% ); transform: translateY( 0% ); }
  100% { -webkit-transform: translateY( calc( 40px - 100% ) ); transform: translateY( calc( 40px - 100% ) ); }
}

@keyframes navigation-off {
  0% { -webkit-transform: translateY( 0% ); transform: translateY( 0% ); }
  100% { -webkit-transform: translateY( calc( 40px - 100% ) ); transform: translateY( calc( 40px - 100% ) ); }
}

@-webkit-keyframes ios-small-in {
  0% { -webkit-transform: translateY( -50px ); transform: translateY( -50px ); }
  100% { -webkit-transform: translateY( 0px ); transform: translateY( 0px ); }
}

@keyframes ios-small-in {
  0% { -webkit-transform: translateY( -50px ); transform: translateY( -50px ); }
  100% { -webkit-transform: translateY( 0px ); transform: translateY( 0px ); }
}

/* mobile */

@media screen and (max-width: 768px) {

  body {
    font-size: 30px;
  }
  
  #loader {
    left: 0px;
  }

  #overlay {
      
  }

  #viewer {
    
  }

  #navigation {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -webkit-transform: translateY( calc( 40px - 100% ) );
        -ms-transform: translateY( calc( 40px - 100% ) );
            transform: translateY( calc( 40px - 100% ) );
  }

  #navigation.navigation-off {
    -webkit-animation: navigation-off 0s ;
            animation: navigation-off 0s ;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  #navigation.navigation-on {
    -webkit-animation: navigation-on 0s ;
            animation: navigation-on 0s ;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  #navigation.navigation-off-anim {
    -webkit-animation: navigation-off 0.3s cubic-bezier(0.44, 0.77, 0.39, 0.99) ;
            animation: navigation-off 0.3s cubic-bezier(0.44, 0.77, 0.39, 0.99) ;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  #navigation.navigation-on-anim {
    -webkit-animation: navigation-on 0.6s cubic-bezier(0.44, 0.77, 0.39, 0.99) ;
            animation: navigation-on 0.6s cubic-bezier(0.44, 0.77, 0.39, 0.99) ;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  .navigation.ios-small-in{
    -webkit-animation: ios-small-in 0.6s cubic-bezier(0.44, 0.77, 0.39, 0.99) ;
            animation: ios-small-in 0.6s cubic-bezier(0.44, 0.77, 0.39, 0.99) ;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  #content {
    padding-top: 50px;
  }
}

/* desktop */

@media screen and (min-width: 768px) {

  body {
      font-size: 16px;
  }
  
  #loader {
    left: 120px;
  }

  #overlay {
      
  }

  #viewer {
    
  }

  #navigation {
    display: -ms-flexbox;
    display: flex;
    height: 50px;
  }

  #content {
    padding-top: 50px;
  }
}


