.bounceIn {
  -webkit-animation: bounceIn 1s ease-out;
          animation: bounceIn 1s ease-out;
}

@-webkit-keyframes bounceIn {
  0% { -webkit-transform: scale(1.5); transform: scale(1.5); opacity: 0; }
  30% { -webkit-transform: scale(1.5); transform: scale(1.5); opacity: 0; }
  50% { -webkit-transform: scale(0.9); transform: scale(0.9); }
  100% { -webkit-transform: scale(1); transform: scale(1); opacity: 1; }
}

@keyframes bounceIn {
  0% { -webkit-transform: scale(1.5); transform: scale(1.5); opacity: 0; }
  30% { -webkit-transform: scale(1.5); transform: scale(1.5); opacity: 0; }
  50% { -webkit-transform: scale(0.9); transform: scale(0.9); }
  100% { -webkit-transform: scale(1); transform: scale(1); opacity: 1; }
}

.anim-wiggle {
  -webkit-animation: anim-wiggle 1s cubic-bezier(.36,.07,.19,.97) both;
          animation: anim-wiggle 1s cubic-bezier(.36,.07,.19,.97) both;
}

@-webkit-keyframes anim-wiggle {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes anim-wiggle {
  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.trans-zoom-in {
  -webkit-transform: scale(1.1)!important;
      -ms-transform: scale(1.1)!important;
          transform: scale(1.1)!important;
  -webkit-transition: all 0.3s!important;
  -o-transition: all 0.3s!important;
  transition: all 0.3s!important;
  -webkit-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
       -o-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
          transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55); 
}


