/* COMMON */

.navigation {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: rgba(255, 255, 255, 0.97);
  overflow: hidden;
  z-index: 10000;
  -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  -ms-flex-pack: justify;
      justify-content: space-between;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
       -o-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
          transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.navigation__logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}
.navigation__links {
  z-index: 20;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: scale(0.02);
      -ms-transform: scale(0.02);
          transform: scale(0.02);
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
       -o-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
          transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.navigation__link {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 50px;
  color: white;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  cursor: pointer;
  font-size: 30px;
}
.navigation__link:hover{
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.navigation__link--selected{
  background-color: white;
  color: #ff0050;
}
.navigation__toggle {
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 80px;
  height: 40px;
  z-index: 10;
}
.navigation__null {
  width: 100px;
  height: 100px;
  overflow: visible;
  display: inline-table;
  position: absolute;
}
.navigation__background {
  position: relative;
  width: 1000px;
  height: 1000px;
  background-color: #ff0050;
  border-radius: 50%;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
       -o-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
          transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  z-index: 2;
}

/* MOBILE */

@media screen and (max-width: 768px) {
  .navigation {
    min-height: 40px;
    max-height: 40px;
    overflow: hidden;
  }
  .navigation--fullscreen {
    min-height: 100%;
    /* background-color: rgba(255, 255, 255, 0); */
  }
  .navigation__logo {
    width: 80px;
    height: 40px;
  }
  .navigation__links {
    /* width: 100%; */
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    /* flex: 1; */
  }
  .navigation__links--show {
    /* display: flex; */
    -ms-flex-item-align: center;
        align-self: center;
  }
  .navigation__link {
    height: 50px;
    -ms-flex-positive: 0;
        flex-grow: 0;
    padding-left: 10px;
    padding-right: 10px;
  }
  .navigation__toggle {}
}

/* DESKTOP */

@media screen and (min-width: 768px) {
  .navigation {
    min-height: 50px;
    max-height: 50px;
  }
  .navigation--fullscreen {
    min-height: 100%;
  }
  .navigation__logo {
    width: 80px;
    height: 50px;
  }
  .navigation__links {
    /* width: 100%; */
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    /* flex: 1; */
  }
  .navigation__links--show {
    /* display: flex; */
    -ms-flex-item-align: center;
        align-self: center;
  }
  .navigation__link {
    height: 50px;
    -ms-flex-positive: 0;
        flex-grow: 0;
    padding-left: 10px;
    padding-right: 10px;
  }
  .navigation__toggle {
    height: 50px;
  }
}


