.block-el {
  position: fixed;
  width: 100%;
  height: 100%;
  color: black;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  z-index: 100000;
  top: 0px;
}

.block-on {
  opacity: 1;
  background-color: rgba(255,255,255,0.98);
}

.block-off {
  opacity: 1;
  -webkit-animation: block-off 1s linear;
          animation: block-off 1s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.loader-background {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
}

.loader-foreground {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100%;
}

.circle {
  width: 200px;
  height: 200px;
  border: dashed 5px #eee;
  border-radius: 50%;
  background-color: white;
}

.loading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0px;
  color: #ccc;
  font-weight: 100;
  font-size: 20px;
}

@-webkit-keyframes block-off {
  from { 
    opacity: 1; 
    -webkit-transform: rotate(0deg); 
            transform: rotate(0deg); 
  }
  to { 
    opacity: 0;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); 
  }
}

@keyframes block-off {
  from { 
    opacity: 1; 
    -webkit-transform: rotate(0deg); 
            transform: rotate(0deg); 
  }
  to { 
    opacity: 0;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); 
  }
}

.spin {
  -webkit-animation: spin 0.5s linear;
          animation: spin 0.5s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.spin-low {
  -webkit-animation: spin 6s linear;
          animation: spin 6s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes spin-low {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin-low {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


